import qs from 'query-string';
import React from 'react';
import { useHistory, useLocation } from 'react-router-dom';

export function usePageTitle(title: string) {
  React.useEffect(() => {
    const previousTitle = document.title;
    document.title = title;
    return () => {
      document.title = previousTitle;
    };
  }, [title]);
}

export function useQuery() {
  return new URLSearchParams(useLocation().search);
}

type Options = {
  array: boolean;
};

const toArrayIfNot = (a: any) => (Array.isArray(a) ? a : [a]);

export function useQueryParam(
  key: string,
  options: Options = { array: false }
): [string, (value: any) => void] {
  const history = useHistory();
  const location = useLocation();

  const query: any = qs.parse(location.search);

  // eslint-disable-next-line
  const value = options.array && query[key] ? toArrayIfNot(query[key]) : query[key];
  const setValue = (newValue: any) => {
    const previousQuery: any = qs.parse(location.search);
    const newQuery = { ...previousQuery, [key]: newValue };
    const newSearch = qs.stringify(newQuery);

    history.push({ search: newSearch });
  };

  return [value, setValue];
}
